@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@500&family=DM+Serif+Display&display=swap");

$color-primary: #3d200d;
$color-secondary: #8e4d22;
$color-tertiary: #e9e7df;

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  box-sizing: border-box;
}

.u {
  &__space-around {
    &--col {
      justify-content: space-between;
      // margin-top: 2em;
      display: flex;
      flex-direction: column;
      align-content: flex-end;
    }
  }
}

.TnC {
  max-width: 144em;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 44.7em auto 33.8em;
  // grid-template-rows: 1fr 1fr 1fr;

  &__aside {
    // padding: 5em 0;
    position: relative;

    &--left {
      padding: 5em 6em;
      background: #e9e7df;
      display: grid;
      grid-template-rows: 34em auto;
      & :nth-child(2) {
        justify-items: baseline;
      }
    }
    &--right {
      padding: 5em 0;
      background: #fff;
    }
  }

  &__HeadingContainer {
    height: 34em;
    // border: 1px solid red;
    margin-left: -6em;
    // overflow:auto;
    
    // &--left {
    //   height: 34em;
    //   position: absolute;
    //   left: 0;
    // }
  }

  &__LogoGraphics {
    // height: 21.25em;
    // border: 1px solid red;
    // padding-top: 8em;
    &--left {
      height: 5.7em;
      width: auto;
    }
  }

  &__paragraph {
    &--left1 {
      color: $color-primary;
      font-family: "DM Serif Display", serif;
      font-size: 2.4em;
      line-height: 1.167em;
      // padding-top: 2em;
    }
    &--left2 {
      color: $color-secondary;
      font-family: "DM Sans", sans-serif;
      font-size: 1.8em;
      line-height: 1.55em;
      // padding-top: 2em;
    }
  }

  &__heading{
    position: absolute;
    left: -1000000em;
  }
}

.TnC-map {
  position: relative;
  padding: 5em 3em;
  &__wrapper {
    border: 2px solid $color-secondary;
    border-radius: 10px;
    height: 100%;
    padding: 2em;
  }
  &__heading {
    font-family: "DM Serif Display", serif;
    font-size: 2.1em;
    line-height: 1.381em;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    color: $color-primary;
  }
  &__subheading {
    color: $color-secondary;
    // color: #8e4d22;
    font-family: DM Sans;
    font-weight: normal;
    font-style: normal;
    // font-weight: bold;
    font-size: 1em;
    line-height: 1.8em;
    text-align: center;
    text-transform: uppercase;
    padding-top: 1em;
  }
  &__map {
    // height: 300px;
    // margin: 10px;
  }

  &__svg {
    margin: 3em 0;
    width: 100%;
    height: 60.3em;
    background-color: steelblue;
  }
  &__brands {
    margin: 2em 0;
  }
  &__brand-logo {
    opacity: 0.2;
    cursor: pointer;
    height: 5.2em;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: 0.4s;
  }

  &__brand-logo:hover {
    transform: translateY(-2px);
    opacity: 0.8;
  }

  &__brand-logo:not(:nth-child(1)) {
    margin-left: 1em;
    mix-blend-mode: normal;
  }
}

.TnC-aside {
  border-radius: 1em;
  width: 33.8em;
  margin-bottom: 2em;

  &__section-right {
    text-align: center;
    background-color: $color-tertiary;
    padding: 3.6rem;
    text-align: center;
    &--1 {
      height: 12em;
      // display: flex;
      // flex-flow: row nowrap;
      // justify-items: center;
      // align-items: center;

      & select {
        width: 100%;
        height: 2.3em;
        background: #ffffff;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 5px;
        font-size: 1.8em;
        padding: 0 1em;
        // Replace default styling
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
        background-image: url('data:image/svg+xml;utf8,<svg width="16" height="14" viewBox="0 0 16 14" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 14L0.205771 0.5L15.7942 0.499998L8 14Z" fill="%233D200D"/></svg>');
        background-position: right 10px top 50%;
        background-repeat: no-repeat;
      }
      & option {
        font-size: 1em;
        // background-color: $color-primary;
      }
      & :focus,
      & :hover {
        outline: none;
        // border: 1px solid lightgreen;
      }
    }
  }

  &__selected-place--name {
    padding: 1em;
    border-radius: 0.5em;
    font-weight: normal;
    font-size: 1.8em;
    color: #ffffff;
    font-family: "DM Sans", sans-serif;
    width: 100%;
    text-align: center;
    background-color: $color-primary;
  }

  &__selected-place--attr {
    font-family: "DM Sans", sans-serif;
    font-size: 1.4em;
    color: $color-primary;
    font-weight: bold;
  }
  & &__icon {
    padding: 3.4em 0;
    // display: block;
  }
}

.select-box{
  display: flex;
  width: 400;
}

.select-box .options-container{
  background: #2f2f2f;
  color: #f5f6fa;
  max-height: 0;
  width: 100%;
  opacity: 0;
  transition: 0.4s;
}

.active-brand {
  opacity: 1;
}