$color--white: #ffffff;
$color-primary: #3d200d;
$color-secondary: #8e4d22;
$color-tertiary: #e9e7df;

* {
  font-family: "DM Sans", sans-serif;
  color: $color-primary;
  font-size: 8px;
  box-sizing: border-box;
}
.container {
  margin-top: 10em;
  padding: 3.2em;
}

.select-box {
  text-align: left;
  display: flex;
  width: auto;
  flex-direction: column;
}

.select-box .options-container {
  background: #2f3640;
  background: $color--white;
  // color: #f5f6fa;
  max-height: 0;
  width: 100%;
  opacity: 0;
  transition: all 0.4s;
  border-radius: 5px;
  overflow: hidden;
  order: 1;
}

.selected {
  background: $color--white;
  border-radius: 5px;
  margin-bottom: 8px;
  // color: #f5f6fa;
  position: relative;
  order: 0;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  font-size: 1.8em;
  border: 1px transparent solid;
}

.selected:hover, .selected:active{
  border: 1px lightgray solid;
}
.selected::after {
  content: "";
  background: url("img/select-appreance.svg");
  background-size: contain;
  background-repeat: no-repeat;
  // background-size: 1em;

  position: absolute;
  height: 100%;
  width: 1em;
  right: 10px;
  top: 0.9em;

  transition: all 0.4s;
}

.select-box .options-container.active {
  max-height: 240px;
  opacity: 1;
  overflow-y: scroll;
}

.select-box .options-container.active + .selected::after {
  transform: rotateX(180deg);
  top: -6px;
}

.select-box .options-container::-webkit-scrollbar {
  width: 8px;
  background: #0d141f;
  border-radius: 0 8px 8px 0;
}

.select-box .options-container::-webkit-scrollbar-thumb {
  background: #525861;
  border-radius: 0 8px 8px 0;
}

.select-box .option,
.selected {
  padding: 12px 24px;
  cursor: pointer;
}

.select-box .option:hover {
  background: #414b57;
}

.select-box label {
  cursor: pointer;
}

.select-box .option .radio {
  display: none;
}
